@import url(https://fonts.googleapis.com/css?family=Roboto:400,700,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  width: 100vw;
  height: 100vh;
  overflow-x: hidden;
  position: relative;
  background-color: black;
  font-family: "Helvetica";
  display: flex;
  flex-direction: row;
}

.us-state-map {
  z-index: 1;
  top: 25%;
  left: 5%;
  width: 90%;
  height: 50%;
  margin: 1rem;
  position: absolute;
}

path {
  z-index: 10;
  pointer-events: all;
}

path:hover {
  opacity: 0.5;
  cursor: pointer;
}

path .tooltiptext {
  visibility: visible;
  width: 120px;
  background-color:white;
  color: #fff;
  text-align: center;
  border-radius: 6px;
  padding: 5px 0;

  /* Position the tooltip */
  position: absolute;
  z-index: 1;
}

path:hover .tooltiptext {
  visibility: visible;
}

@media screen and (max-width: 800px) {
  .us-state-map {
    z-index: 1;
    bottom: 25%;
    left: 5%;
    width: 90%;
    height: 50%;
    margin: 1rem;
    display: absolute;
  }


}
.StateDisplay{display:flex;flex-wrap:wrap;justify-content:center;align-items:center;overflow:hidden;border-radius:20px}.StateDisplay h1{font-weight:800;font-size:2.5rem;width:100%;text-align:center;background-color:#252525;color:#e7e7e7;height:6rem;display:flex;justify-content:center;align-items:center;margin:0}.StateDisplay p{width:40%;height:30%;padding:1rem;font-size:1.25rem;display:flex;flex-direction:column}.StateDisplay p span{font-weight:800;font-size:1.25rem}@media screen and (max-width: 800px){.StateDisplay{height:100%;display:flex;flex-wrap:wrap;justify-content:space-evenly;align-items:center;overflow-y:scroll}.StateDisplay h1{font-weight:800;font-size:2.5rem;width:100%;text-align:center;background-color:#cecece;color:black;height:6rem;display:flex;justify-content:center;align-items:center}.StateDisplay .latest-update{background:transparent;border-radius:0px}.StateDisplay p{width:100%;height:10%;margin:0;display:flex;flex-direction:column}.StateDisplay p:nth-child(odd){background-color:rgba(255,255,255,0.1)}.StateDisplay p span{font-weight:800;font-size:1.25rem}}

.Modal{position:absolute;left:20%;top:10%;height:55%;width:60%;z-index:10;background-color:#505050;color:white;border-radius:20px;transition:opacity 0.75s ease-out;border:2px solid white}.Modal .close-button{height:3rem;width:3rem;position:absolute;top:5%;right:5%;border:3px solid #fff;color:#fff;font-weight:800;font-size:1rem;background:transparent;border-radius:10px;outline:none;cursor:pointer;transition:all 0.75s;display:flex;justify-content:center;align-items:center}.Modal .close-button:hover{transition:all 0.5s;border:3px solid #1f1f1f;color:#1f1f1f}.Modal.hidden{opacity:0;pointer-events:none}@media screen and (max-width: 800px){.Modal{position:absolute;left:5%;top:10%;height:60%;width:90%}.Modal .close-button{height:2rem;width:2rem}.Modal.hidden{opacity:0;pointer-events:none;transition:opacity 0.3s ease-in}}

.AmericaDisplay{width:100%;display:flex;flex-direction:column;justify-content:right}.AmericaDisplay h1{color:white;margin:0;width:100%;display:flex;justify-content:center}.AmericaDisplay div{width:100%;display:flex;justify-content:center;align-items:start}.AmericaDisplay div p{padding:2rem;border-radius:20px;color:#d8d8d8;display:flex;flex-direction:column}.AmericaDisplay div p label{font-size:1.25rem;font-weight:900}.AmericaDisplay .tooltip{position:relative;display:inline-block;border-bottom:1px dotted black}.AmericaDisplay .tooltip .tooltiptext{visibility:hidden;width:120px;background-color:black;color:#fff;text-align:center;border-radius:6px;padding:5px 0;position:absolute;z-index:1}.AmericaDisplay .tooltip:hover .tooltiptext{visibility:visible}@media screen and (max-width: 800px){.AmericaDisplay{width:100%;display:flex;flex-direction:column;align-items:center}.AmericaDisplay h1{color:white;margin:0}.AmericaDisplay div{width:100%;display:flex;flex-wrap:wrap;justify-content:space-evenly}.AmericaDisplay div p{background-color:rgba(255,255,255,0.192);border-radius:20px;width:45%;padding:1rem 0;color:#d8d8d8;display:flex;flex-direction:column}.AmericaDisplay div p label{font-size:1rem;font-weight:900}.us-state-map{z-index:1;bottom:25%;left:5%;width:90%;height:50%;margin:1rem;position:absolute}}

